<template>
  <div id="board">
    <el-header id = "title">Summary of The Data Analysis</el-header>
    <div id ="summary">{{msg}}</div>
    <div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        msg:"    Compared with the tweets data in 2014-2015, the frequency of luxury words increased in 2019 which probably indicates that people’s living standard and purchasing power were improved and they talked more about the luxury goods. In 2020, COVID-19 had a big impact on peoples’ lives and the keywords frequencies of three cities were close to the results in 2014. The frequency of Sydney in 2019 was even fewer than that in 2014. From the AURIN Data perspective, the total income divided by population in Sydney was higher than Melbourne and Adelaide was the lowest one among the three cities.  The ranking of total income per capita applies to our tweets data analysis(frequency of luxury words) which illustrates that personal income has a strong positive relationship with the frequency of luxury words on tweets. On the other hand, the reason that differences of tweets data were larger than the actual personal income(AURIN Data) in the same year could be attributed to the poverty gap in developed cities."
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #board {
    height: auto;
    width: auto;
    border: 1px solid red;
    line-height: auto;

  }
  #title{
    background-color: #606266;
    font-size: 40px;
    text-align: center;
    line-height: 100px;

  }
  #summary {
    text-indent: 2em;
    margin: 80px;
    font-weight: normal;
    font-size: 40px;
    line-height: 150%;
    text-align: left;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 30px;
  }
  a {
    color: #42b983;
  }
  div{
    font-size: 20px;
    line-height: 30px;
  }
  #names{
    margin: 50px;
  }
</style>



