<!-- mapbox -->
<template>
  <div id="map">
  </div>
</template>

<script>
// import axios from 'axios'
export default {
  data () {
    return {
      currentMap: 'streets-v11',
      map: '',
      GeoMap: ''
    }
  },
  components: {},
  created () {
    this.$mapboxgl.setRTLTextPlugin(
      'https://api.mapbox.com/mapbox-gl-js/plugins/mapbox-gl-rtl-text/v0.1.0/mapbox-gl-rtl-text.js'
    )
    this.map.addControl(
      new this.$mapboxgl.MapboxLanguage({
        defaultLanguage: 'en'
      })
    )
  },
  mounted () {
    this.DB.DataBase.get('22').then(doc => {
      // handle doc
      console.log(doc)
      this.initmap(doc)
    }).catch(function (err) {
      console.log(err)
    })
    // this.initmap()
  },
  computed: {},
  methods: {
    initmap (jsonMap) {
      console.log('initmap')
      // var jsonMap = JSON.parse(sessionStorage.getItem('pointJson'))
      this.$mapboxgl.accessToken =
          'pk.eyJ1Ijoid2VpaGFuZzEiLCJhIjoiY2tvM3M4MDZ6MDg2azJ3b2FvM3FrZ2hmeiJ9.fHmcUQQlAnHZOuD-apa7Jw'
      this.map = new this.$mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/mapbox/dark-v10',
        center: [-79.999732, 40.4374],
        zoom: 11,
        attributionControl: false
      })

      // 注册点击事件
      this.map.on('click', function () {
        console.log('click')
      })

      // function getGeoJson () {
      //   axios.get('./static/trees.geojson').then(response => {
      //     console.log('success')
      //     return response.data
      //   }, response => {
      //     console.log('error')
      //   })
      // }

      this.map.on('load', function () {
        this.addSource('trees', {
          type: 'geojson',
          // data: getGeoJson()
          // data: '/static/trees.geojson'
          data: jsonMap
        })
        // add heatmap layer here
        this.addLayer({
          id: 'trees-heat',
          type: 'heatmap',
          source: 'trees',
          maxzoom: 15,
          paint: {
            // increase weight as diameter breast height increases
            'heatmap-weight': {
              property: 'dbh',
              type: 'exponential',
              stops: [
                [1, 0],
                [62, 1]
              ]
            },
            // increase intensity as zoom level increases
            'heatmap-intensity': {
              stops: [
                [11, 1],
                [15, 3]
              ]
            },
            // assign color values be applied to points depending on their density
            'heatmap-color': [
              'interpolate',
              ['linear'],
              ['heatmap-density'],
              0, 'rgba(236,222,239,0)',
              0.2, 'rgb(208,209,230)',
              0.4, 'rgb(166,189,219)',
              0.6, 'rgb(103,169,207)',
              0.8, 'rgb(28,144,153)'
            ],
            // increase radius as zoom increases
            'heatmap-radius': {
              stops: [
                [11, 15],
                [15, 20]
              ]
            },
            // decrease opacity to transition into the circle layer
            'heatmap-opacity': {
              default: 1,
              stops: [
                [14, 1],
                [15, 0]
              ]
            }
          }
        }, 'waterway-label')
        // add circle layer here
        this.addLayer(
          {
            'id': 'trees-point',
            'type': 'circle',
            'source': 'trees',
            'minzoom': 14,
            'paint': {
              'circle-radius': {
                'property': 'dbh',
                'type': 'exponential',
                'stops': [
                  [{ zoom: 15, value: 1 }, 5],
                  [{ zoom: 15, value: 62 }, 10],
                  [{ zoom: 22, value: 1 }, 20],
                  [{ zoom: 22, value: 62 }, 50]
                ]
              },
              'circle-color': {
                'property': 'dbh',
                'type': 'exponential',
                'stops': [
                  [0, 'rgba(236,222,239,0)'],
                  [10, 'rgb(236,222,239)'],
                  [20, 'rgb(208,209,230)'],
                  [30, 'rgb(166,189,219)'],
                  [40, 'rgb(103,169,207)'],
                  [50, 'rgb(28,144,153)'],
                  [60, 'rgb(1,108,89)']
                ]
              },
              'circle-stroke-color': 'white',
              'circle-stroke-width': 1,
              'circle-opacity': {
                'stops': [
                  [14, 0],
                  [15, 1]
                ]
              }
            }
          },
          'waterway-label'
        )
      })
    },
    handleSelect (item) {
      this.map.setStyle('mapbox://styles/mapbox/' + item)
    }
  }
}
</script>

<style>
  #map {
    height: 800px;
    width: auto;
    border: 1px solid red;
  }
</style>



