import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Theme from '../components/Theme'
import Map from '../components/Map'
import Twitter14 from '../components/Twitter14'
import Twitter19 from '../components/Twitter19'
import Twitter20 from '../components/Twitter20'
import Staff from '../components/Staff'
import AurinIncome from '../components/AurinIncome'
import AurinPop from '../components/AurinPop'
import Chart from '../components/Chart'
import Summary from '../components/Summary'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/staff',
      component: Staff
    },
    {
      path: '/aurinIncome',
      component: AurinIncome
    },
    {
      path: '/aurinPop',
      component: AurinPop
    },
    {
      path: '/theme',
      component: Theme
    },
    {
      path: '/map',
      component: Map
    },
    {
      path: '/twitter14',
      component: Twitter14
    },
    {
      path: '/twitter19',
      component: Twitter19
    },
    {
      path: '/twitter20',
      component: Twitter20
    },
    {
      path: '/chart',
      component: Chart
    },
    {
      path: '/summary',
      component: Summary
    }
  ]
})



