<template>
  <div id="app">
    <el-row :gutter="40">
      <el-col :span="12" class="echarts-item">
        <div id="main" style="width: 800px;height:400px;"></div>
      </el-col>
      <el-col :span="12" class="echarts-item">
        <div id="chartPop" class="pie-wrap" style="width: 800px;height:400px;"></div>
      </el-col>
    </el-row>
    <el-row :gutter="40">
      <el-col :span="12" class="echarts-item">
        <div id="chartTweet2014" class="pie-wrap" style="width: 800px;height:400px; margin-top: 20px"></div>
      </el-col>
      <el-col :span="12" class="echarts-item">
        <div id="chartIncome" class="pie-wrap" style="width: 800px;height:400px; margin-top: 20px"></div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
require('echarts/theme/macarons')
export default {
  name: 'app',
  methods: {
    drawPopChart () {
      let mytextStyle = {
        color: '#333',
        fontSize: 18
      }
      let mylabel = {
        show: true,
        position: 'right',
        offset: [30, 40],
        formatter: '{b} : {c} ({d}%)',
        textStyle: mytextStyle
      }
      this.chartPie = this.$echarts.init(document.getElementById('chartPop'), 'macarons')
      this.chartPie.setOption({
        title: {
          text: 'Population in 2014 (Million)',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          data: ['Sydney', 'Melbourne', 'Adelaide'],
          left: 'center',
          top: 'bottom',
          orient: 'horizontal'
        },
        series: [
          {
            name: 'Population (Million)',
            type: 'pie',
            radius: ['0%', '70%'],
            center: ['50%', '50%'],
            data: [
              {value: 4.840628, name: 'Sydney'},
              {value: 4.440328, name: 'Melbourne'},
              {value: 1.304631, name: 'Adelaide'},
            ],
            animationEasing: 'cubicInOut',
            animationDuration: 2600,
            label: {
              emphasis: mylabel
            }
          }
        ]
      })
    },
    drawIncomeChart () {
      let mytextStyle = {
        color: '#333',
        fontSize: 18
      }
      let mylabel = {
        show: true,
        position: 'right',
        offset: [30, 40],
        formatter: '{b} : {c} ({d}%)',
        textStyle: mytextStyle
      }
      this.chartPie = this.$echarts.init(document.getElementById('chartIncome'), 'macarons')
      this.chartPie.setOption({
        title: {
          text: 'Total Income per capita in 2014-2015',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          data: ['Sydney', 'Melbourne', 'Adelaide'],
          left: 'center',
          top: 'bottom',
          orient: 'horizontal'
        },
        series: [
          {
            name: 'Average Income in 2014-2015',
            type: 'pie',
            radius: ['50%', '70%'],
            center: ['50%', '50%'],
            data: [
              // {value: 335, name: '直接访问'},
              // {value: 310, name: '邮件营销'},
              // {value: 234, name: '联盟广告'},
              // {value: 135, name: '视频广告'},
              // {value: 1548, name: '搜索引擎'}
              {value: (183938.52 / 4.840628), name: 'Sydney'},
              {value: (153666.98 / 4.440328), name: 'Melbourne'},
              {value: (40763.35 / 1.304631), name: 'Adelaide'},
            ],
            animationEasing: 'cubicInOut',
            animationDuration: 2600,
            label: {
              emphasis: mylabel
            }
          }
        ]
      })
    },
    drawTweetChart () {
      let mytextStyle = {
        color: '#333',
        fontSize: 18
      }
      let mylabel = {
        show: true,
        position: 'right',
        offset: [30, 40],
        formatter: '{b} : {c} ({d}%)',
        textStyle: mytextStyle
      }
      this.chartPie = this.$echarts.init(document.getElementById('chartTweet2014'), 'macarons')
      this.chartPie.setOption({
        title: {
          text: 'Luxury Product Tweets per Million people in 2014-2015',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          // data: ['直接访问', '邮件营销', '联盟广告', '视频广告', '搜索引擎'],
          data: ['Sydney', 'Melbourne', 'Adelaide'],
          left: 'center',
          top: 'bottom',
          orient: 'horizontal'
        },
        series: [
          {
            name: 'Tweets',
            type: 'pie',
            radius: ['50%', '70%'],
            center: ['50%', '50%'],
            data: [
              // {value: 335, name: '直接访问'},
              // {value: 310, name: '邮件营销'},
              // {value: 234, name: '联盟广告'},
              // {value: 135, name: '视频广告'},
              // {value: 1548, name: '搜索引擎'}
              {value: 1416 / 4.840628, name: 'Sydney'},
              {value: 1201 / 4.440328, name: 'Melbourne'},
              {value: 204 / 1.304631, name: 'Adelaide'},
            ],
            animationEasing: 'cubicInOut',
            animationDuration: 2600,
            label: {
              emphasis: mylabel
            }
          }
        ]
      })
    },
    drawChart () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById('main'), 'macarons')
      // 指定图表的配置项和数据
      let option = {
        title: {
          text: 'Luxury Products Tweets'
        },
        tooltip: {},
        legend: {
          data: ['Sydney', 'Melbourne', 'Adelaide']
        },
        xAxis: {
          data: ['2014-2015 Average', '2019', '2020']
        },
        yAxis: {},
        series: [

          {
            name: 'Sydney',
            type: 'bar',
            data: [1416 / 2, 724, 561]
          },
          {
            name: 'Melbourne',
            type: 'bar',
            data: [1201 / 2, 883, 802]
          },
          {
            name: 'Adelaide',
            type: 'bar',
            data: [204 / 2, 188, 107]
          }
        ]
      }
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option)
    }
  },
  mounted () {
    this.drawChart()
    this.drawPopChart()
    this.drawIncomeChart()
    this.drawTweetChart()
  }
}
</script>




