<!-- mapbox -->
<template>
  <div id="map">
    <el-header id = "title">Number of Tweets Mentioned Luxury Products in 2019</el-header>
    <div>
    </div>
  </div>
</template>

<script>
import {AxiosInstance as axios} from 'axios'

export default {
  data () {
    return {
      currentMap: 'streets-v11',
      map: '',
      GeoMap: this.$data.mapJson,
      title: 'Aurin Income'
    }
  },
  components: {},
  created () {
    // this.$mapboxgl.setRTLTextPlugin(
    //   'https://api.mapbox.com/mapbox-gl-js/plugins/mapbox-gl-rtl-text/v0.1.0/mapbox-gl-rtl-text.js'
    // )
    // this.map.addControl(
    //   new this.$mapboxgl.MapboxLanguage({
    //     defaultLanguage: 'en'
    //   })
    // )
  },
  mounted () {
    // this.getUserInfo()
    this.DB.TWBase.get('loc_year_count_2019').then(doc => {
      this.initmap(doc)
    }).catch(function (err) {
      console.log(err)
    })
    // this.initmap()
  },
  computed: {},
  methods: {
    updateData (data, mapjson) {
      mapjson.features[0].properties.population = data.Sydney
      mapjson.features[1].properties.population = data.Melbourne
      mapjson.features[2].properties.population = data.Adelaide

      return mapjson
    },

    initmap (jsonMap) {
      var mapboxgl = this.$mapboxgl
      console.log('initmap')
      console.log(this.$mapboxgl)
      // var jsonMap = JSON.parse(sessionStorage.getItem('mapJson'))
      this.$mapboxgl.accessToken =
          'pk.eyJ1Ijoid2VpaGFuZzEiLCJhIjoiY2tvM3M4MDZ6MDg2azJ3b2FvM3FrZ2hmeiJ9.fHmcUQQlAnHZOuD-apa7Jw'
      var map = new this.$mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/mapbox/dark-v10',
        // center: [118.3008,25.9277],
        center: jsonMap.cp,
        zoom: 6,
        attributionControl: false
      })

      map.on('load', function () {
        this.addSource('fillSourceID', {
          type: 'geojson',
          // data: 'static/rawGeojson.json'
          // data: '/static/pro35.json'
          // data: '/static/test_goe.json'
          // data: 'static/greater_income_goe.json'
          // data: 'static/greater_Population_goe.json'
          data: jsonMap
        })
        // add heatmap layer here
        map.addLayer({
          id: 'filledID',
          type: 'fill',
          source: 'fillSourceID',
          paint: {
            'fill-color':
                  {
                    property: 'value', // this will be your density property form you geojson
                    stops: [
                      [0, '#ffd0a6'],
                      [300, '#ffd0a6'],
                      [600, '#ffaa7f'],
                      [900, '#ff704e'],
                      [1200, '#f04040'],
                      [1500, '#b50a09']
                    ]
                  },
            'fill-opacity': 1 /* 透明度 */
          }
        },
        'waterway-label'
        )
        // 注册点击事件
        map.on('click', 'filledID', function (e) {
          new mapboxgl.Popup()
            .setLngLat(jsonMap.cp)
            .setHTML('<div>' + 'Area: ' + e.features[0].properties.name + '</div>' + '<div>' + 'Number of Tweets: ' + e.features[0].properties.value + '</div>')
            .addTo(map)
        })
      })
    },
    handleSelect (item) {
      this.map.setStyle('mapbox://styles/mapbox/' + item)
    }
  }}
</script>

<style>
  #map {
    height: 800px;
    width: auto;
    border: 1px solid red;
    line-height: auto;
  }
  #title{
    background-color: #606266;
    font-size: 40px;
    text-align: center;
    line-height: 100px;

  }
  .mapboxgl-popup{
    transform:none !important;
    top: 50%;
    left: 10px;
    margin:0px;
  }
  .mapboxgl-popup-content div{
    width:500px;
    height:auto;
    float:left;
    display:inline-block;;
    line-height: 30px;
    background-color: black;
    color: white;
    text-align-all: center;
  }
  .mapboxgl-popup-anchor-top .mapboxgl-popup-tip,
  .mapboxgl-popup-anchor-bottom .mapboxgl-popup-tip,
  .mapboxgl-popup-anchor-center .mapboxgl-popup-tip,
  .mapboxgl-popup-anchor-left .mapboxgl-popup-tip,
  .mapboxgl-popup-anchor-right .mapboxgl-popup-tip,
  .mapboxgl-popup-anchor-bottom-right .mapboxgl-popup-tip,
  .mapboxgl-popup-anchor-bottom-left .mapboxgl-popup-tip,
  .mapboxgl-popup-anchor-top-right .mapboxgl-popup-tip,
  .mapboxgl-popup-close-button,
  .mapboxgl-popup-anchor-top-left .mapboxgl-popup-tip{
    display:none !important;
  }
</style>



